let emoji=[];
for (const key in dataObj) {
  let title=key;
  let list=[];
  for (const k in dataObj[key]) {
    let obj={
      key:k,
      value:dataObj[key][k]
    };
    list.push(obj)
  }
  emoji.push({
    title,
    list
  })
};
export default emoji;



